<template>
  <div id="app">
    <el-container>
      <el-main>
        <el-row>
          <h1>Hyphenate Me! v3.0.0</h1>
          <h2>created with ❤ by Daniel Rienas</h2>
          <p>
            <el-link href="https://github.com/blurrryy/" target="_new">https://github.com/blurrryy/</el-link>
          </p>
          <img alt="hyphe-lib-ger npm badge" src="https://img.shields.io/npm/v/hyphe-lib-ger.svg">
          <br>
          <br>
        </el-row>
        <el-row>
          <el-col :span="12" :offset="6">
            <el-input type="textarea" v-model="text" placeholder="Enter your text here" rows="10"></el-input>
          </el-col>
        </el-row>
        <el-row>
          <p>
            Switch off for HTML Output
            <br>
            <el-switch v-model="switchOutput"></el-switch>
          </p>
        </el-row>
        <el-row>
          <el-col :span="12" :offset="6">
            <h2 v-html="output" v-if="switchOutput"></h2>
            <h3 v-if="!switchOutput" style="word-break: break-all;">{{output}}</h3>
          </el-col>
        </el-row>
      </el-main>
    </el-container>
  </div>
</template>

<script>
export default {
  name: "app",
  data() {
    return {
      text: "",
      switchOutput: true,
      output: ""
    };
  },
  watch: {
    async text(val) {
      let op = await this.$hyphe(val);
      op = op.replace(/&#10;/g, "<br>");
      this.output = op;
    }
  }
};
</script>

<style>
#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
</style>

